@import '../_includes';


$color-white : #ffffff;
$color : #404040;
$bg : #e67e22;


body {
    font-family: 'Open Sans',-apple-system,system-ui,BlinkMacSystemFont; }

.top-header {
    padding : 10px 0px;
    background: $bg;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999999999999999999999999999;
    .main-menu {
        width: 100%;
        .bg-dark {
            background: transparent !important; }
        .navbar-toggler {
            color:#fff!important {}
            border: none !important;
            outline: none !important; }
        .logo {
            text-decoration: none;
            color: $color-white;
            font-size: 32px;
            font-family: 'iCiel Pacifico',-apple-system,system-ui,BlinkMacSystemFont; }
        .navbar-collapse {
            justify-content: space-evenly !important;
            ul {
                list-style: none;
                margin-bottom: 0px;
                li {
                    padding: 0px 20px;
                    a {
                        text-decoration: none;
                        color : $color-white;
                        font-size: 18px;
                        text-transform: uppercase;
                        &:after {
                            display: block;
                            content: '';
                            border-bottom: 2px solid $color-white;
                            transform: scale(0);
                            transition: transform 250ms ease-in-out; }
                        &:hover:after {
                            transform: scale(1);
                            @media (max-width:767px) {
                                font-size: 16px; } } } }
                .active {
                    background: #404040; } }
            .social-menu {
                .facebook {
                    padding: 0px; } } } } }

@mixin job-name {
    color: $color-white;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 2px; }




.main-block {
    .bg-banner {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        background: url('../../images/s-5.jpg');
        background-size: cover;
        height: 700px;
        overflow: hidden;
        &:after {
            background: $bg;
            height: 500px;
            content: "";
            width: 500px;
            position: absolute;
            z-index: 999;
            left: -350px;
            transform: rotate(45deg);
            border-radius: 15px;
            @media (max-width:767px) {
                display: none; }
            @media (min-width :768px) and (max-width:991px) {
                display: none; } }
        &:before {
            background: $bg;
            border: 10px solid $bg;
            height: 250px;
            content: "";
            width: 250px;
            border-radius: 15px;
            transform: rotate(45deg);
            position: absolute;
            bottom: -125px;
            right: 45px;
            @media (max-width:767px) {
                display: none; }
            @media (min-width :768px) and (max-width:991px) {
                display: none; } }
        .img-banner {
            padding: 0px 15px;
            .jobs-and-name {
                .name {
                    p {
                        font-size: 50px;
                        color: #f1c40f !important;
                        @include job-name;
                        @media (max-width:767px) {
                            font-size: 30px; } } }
                .jobs {
                    p {
                        font-size: 35px;
                        @include job-name;
                        @media (max-width:767px) {
                            font-size: 25px; } } }
                .contacts-me {
                    .link {
                        a {
                            text-decoration: none;
                            font-size: 45px;
                            color: #fff; } }
                    .my-cv {
                        position: absolute;
                        right: 100px;
                        bottom: 25px;
                        a {
                            text-decoration: none;
                            color: $color-white;
                            border-radius: 50px;
                            padding: 5px 20px;
                            background: $color;
                            animation: dhBlink 0.3s infinite alternate;
                            display: inline-block; }
                        @media (max-width:767px) {
                            position: inherit;
                            margin-top: 15px; }
                        @media (min-width :768px) and (max-width:991px) {
                            position: inherit;
                            margin-top: 15px; } } } } }
        .box-img {
            position: absolute;
            left: 15px;
            z-index: 9;
            transform: rotate(45deg);
            width: 250px;
            height: 250px;
            border: 10px solid $color-white;
            background: $color-white;
            border-radius: 15px;
            @media (max-width:767px) {
                display: none; }
            @media (min-width :768px) and (max-width:991px) {
                 display: none; } } } }




@mixin heading-block {
    color: $bg;
    text-transform: uppercase;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    display: flex;
    flex-basis: 100%; }

@mixin heading-block-after {
        content: "";
        flex-grow: 1;
        background: $color;
        height: 1px;
        margin: 0px 5px; }


//block-introduction
.block-content.block-intoduction {
    padding: 35px 0px;
    .heading-block {
        @include heading-block;
        &:before,&:after {
            @include heading-block-after; }
        @media screen and (device-aspect-ratio: 40/71) {
            font-size: 20px; }
        @media (max-width:767px) {
            font-size: 20px; } }
    .img-me {
        overflow: hidden;
        border-radius: 15px;
        border: 5px solid $bg; }
    .table {
        border: none !important;
        @media screen and (device-aspect-ratio: 40/71) {
            font-size: 13px; }
        td {
            padding: 25px 7px 0px 0px !important;
            @extend .table;
            @media (max-width:767px) {
                padding: 25px 7px 0px 0px !important; } }
        .name {
            font-weight: 700;
            color: $bg; } } }
.block-content.block-experience {
    padding: 35px 0px;
    .heading-block {
        @include heading-block;
        &:before,&:after {
            @include heading-block-after; }
        @media screen and (device-aspect-ratio: 40/71) {
            font-size: 20px; }
        @media (max-width:767px) {
            font-size: 20px; } }
    .timeline {
        max-width: 1200px;
        margin: 0 auto;
        position: relative;
        &:after {
            content: '';
            position: absolute;
            width: 6px;
            background-color: $bg;
            top: 0;
            bottom: 0;
            left: 50%;
            margin-left: -3px;
            @media (max-width:767px) {
                left: 5px; } }
        .main {
            padding: 10px 40px;
            position: relative;
            background-color: inherit;
            width: 50%;
            @media (max-width:767px) {
                width: 100%;
                padding: 10px 0px 10px 40px;
                &:before {
                    left: 60px;
                    border: medium solid white;
                    border-width: 10px 10px 10px 0;
                    border-color: transparent white transparent transparent; } }
            &:after {
                content: '';
                position: absolute;
                width: 25px;
                height: 25px;
                right: -12px;
                background-color: $color;
                border: 4px solid $color;
                top: 15px;
                border-radius: 50%;
                z-index: 1; }
            .content {
                padding: 20px 30px;
                background-color: $bg;
                position: relative;
                border-radius: 6px;
                color: $color-white; } }
        .left {
            left: 0;
            &:before {
                content: " ";
                height: 0;
                position: absolute;
                top: 22px;
                width: 0;
                z-index: 1;
                right: 30px;
                border: medium solid $color;
                border-width: 10px 0 10px 10px;
                border-color: transparent transparent transparent $bg;
                @media (max-width: 767px) {
                        transform: rotate(182deg);
                        left: 30px; } }
            @media (max-width:767px) {}
            &:after {
                @media (max-width:767px) {
                    left: -8px; } } }
        .right {
            left: 50%;
            @media (max-width:767px) {
                left: 0%; }
            &:before {
                content: " ";
                height: 0;
                position: absolute;
                top: 22px;
                width: 0;
                z-index: 1;
                left: 30px;
                border: medium solid $color;
                border-width: 10px 10px 10px 0;
                border-color: transparent $bg transparent; }
            &:after {
                left: -13px;
                @media (max-width:767px) {
                    left: -8px; } } } }
    .continue {
        color: $bg;
        text-transform: uppercase; }
    //skills
    .wapperr {
        max-width: 750px;
        margin: 0 auto;
        .skill {
            margin-bottom: 35px;
            position: relative;
            overflow: hidden;
            width: 100%;
            p {
                font-size: 18px;
                font-weight: 700;
                color: $color;
                margin: 0; }
            &:before {
                width: 100%;
                height: 20px;
                content: '';
                display: block;
                position: absolute;
                background: #dddddd;
                bottom: 0; }
            .skill-bar {
                width: 100%;
                height: 20px;
                background: $bg;
                display: block;
                position: relative;
                span {
                    position: absolute;
                    top: -30px;
                    padding: 0px;
                    font-size: 18px;
                    padding: 3px 0;
                    font-weight: 500; } }
            .skill1 {
                width: 95%;
                .skill-count1 {
                    right: 0px; } }
            .skill2 {
                width: 90%;
                .skill-count2 {
                    right: 0px; } }
            .skill3 {
                width: 70%;
                .skill-count3 {
                    right: 0px; } }
            .skill4 {
                width: 75%;
                .skill-count4 {
                    right: 0px; } }
            .skill5 {
                width: 65%;
                .skill-count5 {
                    right: 0px; } }
            .skill6 {
                width: 80%;
                .skill-count6 {
                    right: 0px; } }
            .skill7 {
                width: 50%;
                .skill-count7 {
                    right: 0px; } } } }
    //skills



    //project
    .img-project {
        height: 400px;
        overflow: hidden;
        display: flex;
        align-items: center;
        border: 1px solid $bg;
        border-radius: 20px;
        cursor: pointer;
        img {
            -webkit-transition: .3s ease-in-out;
            -moz-transition: .3s ease-in-out;
            -o-transition: .3s ease-in-out;
            transition: .3s ease-in-out {
             transform: scale(1); }
            &:hover {
                transform: scale(1.2);
                -webkit-transition: .3s ease-in-out;
                -moz-transition: .3s ease-in-out;
                -o-transition: .3s ease-in-out;
                transition: .3s ease-in-out; } } }

 }    //project
//block-introduction



//footer
.footer {
    background: $bg;
    padding: 25px 0px;
    .design-me {
        p {
            font-size: 25px;
            color: $color-white;
            font-family: 'iCiel Pacifico',-apple-system,system-ui,BlinkMacSystemFont;
            @media (max-width:767px) {
                text-align: center; } } }
    .contacts-me {
        p {
            font-size: 22px;
            @media (max-width:767px) {
                font-size: 15px; }
            @media (min-width :768px) and (max-width:991px) {
                 font-size: 15px; }
            a,i {
                color: $color-white; } } } }
//footer





//  // img.image(src="assets/images/banner111.jpg", alt="").img-fluid
//      // background: url('../../images/banner111.jpg')
@media (max-width:767px) {
    .top-header {
        .main-menu {
            .navbar-nav {
                margin-top: 20px;
                li {
                    padding: 0px !important;
                    margin: 10px 0px; } }
            .logo {
                font-size: 22px; }

            .social-menu {
                .facebook {
                    margin-top: 10px !important; } } } } }
@media (min-width :768px) and (max-width:991px) {
    .top-header {
        .main-menu {
            .navbar-nav {
                margin-top: 20px;
                li {
                    padding: 0px !important;
                    margin: 10px 0px; } }
            .social-menu {
                .facebook {
                    margin-top: 10px !important; } } } } }

@keyframes dhBlink {
    0% {
        transform: scale(1); }
    100% {
        transform: scale(1.08); } }
